const t = TrelloPowerUp.iframe();

const finish = () => {
  t.closePopup();
};

const toggleLoad = () => {
  ['edit-date', 'loading'].forEach((id) => {
    const el = document.getElementById(id);

    el.className = el.className === 'hidden' ? '' : 'hidden';
  });
};

window.addYear = () => {
  toggleLoad();

  t.cards('all').then((cards) => {
    console.log(JSON.stringify(cards, null, 2));

    // t.set(card.id, 'shared', '', value)
    toggleLoad();
  });
};

window.removeYear = () => {
  alert('remove year');
};

window.unsetCompleted = () => {
  alert('unset completed');
  finish();
};

window.closePopup = () => {
  finish();
};

t.render(() => {
  t.sizeTo('#edit-date').done();
});
